import { createTextWithString } from '@ali/4ever-cangjie';
import { JsonML, createLinkify, matchesLink } from '@ali/4ever-utils';

export type Matched = {
  index: number;
  lastIndex: number;
  url: string;
};

const linkify = createLinkify();

export default {
  name: 'link',

  match: (line: string): boolean => {
    return !!matchesLink(linkify, line);
  },

  convert: (line: string): JsonML[] => {
    const matches: Matched[] = matchesLink(linkify, line)!;
    const children: JsonML[] = [];
    let lastIndex = 0;
    matches.forEach(match => {
      if (match.index > lastIndex) {
        children.push(
          createTextWithString(line.substring(lastIndex, match.index)),
        );
      }
      if (match.index === 0) {
        children.push(createTextWithString(''));
      }
      children.push([
        'a',
        { href: match.url },
        createTextWithString(line.substring(match.index, match.lastIndex)),
      ]);

      if (match.lastIndex === line.length) {
        children.push(createTextWithString(''));
      }
      lastIndex = match.lastIndex;
    });
    if (lastIndex < line.length) {
      children.push(createTextWithString(line.substring(lastIndex)));
    }
    return children;
  },
};
